import { yupResolver } from "@hookform/resolvers/yup";
import { Button, Dialog, DialogActions, DialogContent, DialogTitle } from "@mui/material";
import { NexusInput } from "components";
import { useState } from "react";
import { FormProvider, useForm } from "react-hook-form";
import { useTranslation } from "react-i18next";
import { toast } from "react-toastify";
import * as yup from "yup";
import Styles from "./index.module.css";
import { PaymentMethods } from "./payment-methods";
import { get } from "lodash";
import { planService } from "services";

interface Props {
  isOpen: boolean;
  setIsOpen: (isOpen: boolean) => void;
  loadUserPlan: () => Promise<void>;
}

export const AddUsersPlanDialog = ({ isOpen, setIsOpen, loadUserPlan }: Props) => {
  const { t } = useTranslation();
  const [isLoading, setIsLoading] = useState(false);

  const requiredMessage = t("common.is_required");

  const validationSchema = yup.object().shape({
    users: yup
      .number()
      .moreThan(
        0,
        `${t("user_plan_management.users_to_buy_for_the_plan")} ${t(
          "common.must_be_greater_than_or_equal_to",
        )} 1`,
      )
      .required(`${t("user_plan_management.users_to_buy_for_the_plan")} ${requiredMessage}`),
    paymentMethodId: yup
      .number()
      .typeError(`${t("billing.payment_method")} ${requiredMessage}`)
      .required(`${t("billing.payment_method")} ${requiredMessage}`),
  });

  const methods = useForm({
    resolver: yupResolver(validationSchema),
    defaultValues: {
      paymentMethodId: 0,
      users: 0,
    },
  });

  const onSubmit = async (paymentMethodId: number, users: number) => {
    if (isLoading) return;
    try {
      setIsLoading(true);
      await planService.payPerUsers(paymentMethodId, users);
      methods.reset();
      await loadUserPlan();
      setIsLoading(false);
      setIsOpen(false);
      toast.success(t("user_plan_management.accounts_added_successfully"));
    } catch (e) {
      const message = get(e, "response.data.message");
      toast.error(t(message));
    } finally {
      setIsLoading(false);
    }
  };
  return (
    <Dialog className={Styles.dialog} open={isOpen}>
      <DialogTitle>{t("user_plan_management.add_users_to_plan")}</DialogTitle>
      <FormProvider {...methods}>
        <form
          onSubmit={methods.handleSubmit(
            async ({ paymentMethodId, users }) => onSubmit(paymentMethodId, users),
          )}
          id="add-users-plan-form"
          data-testid="add-users-plan-form"
        >
          <DialogContent className={Styles.content}>
            <PaymentMethods />
            <NexusInput
              label={t("user_plan_management.users_to_buy_for_the_plan")}
              name="users"
              type="number"
            />
          </DialogContent>
          <DialogActions>
            <Button
              onClick={() => {
                methods.reset();
                setIsOpen(false);
              }}
            >
              {t("common.cancel")}
            </Button>
            <Button disabled={isLoading} type="submit">
              {t("user_plan_management.add_users_to_plan")}
            </Button>
          </DialogActions>
        </form>
      </FormProvider>
    </Dialog>
  );
};
